.AffirmationSwipeIndicator--correct {
  background-color: #0a0a;
  top: 11vh;
  height: 89vh;
  position: absolute;
  z-index: 1;
}

.AffirmationSwipeIndicator--incorrect {
  background-color: #a00a;
  top: 11vh;
  height: 89vh;
  width: 100vw;
  position: absolute;
  z-index: 1;
}



